import React, { useState } from "react";
import styled from "styled-components";
import rectangleBox from "../image/Rectangle 3.png";
import { useNavigate } from 'react-router-dom';
import logo from "../image/Group 155.png";
import line1 from "../image/line1.png";
import "./Fonts.css";

const Title = styled.a`
  display: block;
  padding-left: 21px; //    
  text-decoration: none;

  margin-top: 12px;
  font-size: 22px; //   

  // selected prop   
  ${(props) =>
    props.selected &&
    `
    color: white;
    font-family: SDB;
    weight: 700;
    line-height: 41px;
    background-image: url(${rectangleBox});
    background-repeat: no-repeat;
  `}

  ${(props) =>
    props.others &&
    `
    color:#545454;
    weight: 600;
    
    line-height: 41px;
    font-family: SDSB;
  `}
`;

function LeftCategories({ onCategoryChange, onLogoClick }) {
  const category_title = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  //    Dictionary

  const [selectedCategory, setSelectedCategory] = useState("  ");
  const navigate = useNavigate();

  const handleCategoryClick = (category) => {
    setSelectedCategory(category);
    onCategoryChange(category); // <--      
  };

  const handleLogoClick = (e) => {
    e.preventDefault(); // Prevent the default link click action
    handleCategoryClick("  ");
    onCategoryChange("  ");
    navigate('/');

    if (onLogoClick) onLogoClick(); //  : HomePage.js     
  };

  return (
    <div>
      <div
        style={{
          position: "absolute",
          width: 305,
          borderRight: "1px solid rgba(219, 219, 219, 1)",
          height: "100%",
        }}
      >
        <div
          style={{
            display: "block",
            flexDirection: "column",
            alignItems: "flex-start",
            width: 305,
            textAlign: "left",
            overflow: "hidden",
            paddingBottom: "10px",
          }}
        >
          <img
            src={logo}
            onClick={handleLogoClick}
            alt="logo"
            style={{
              position: "absolute",
              left: 38,
              top: 0,
              cursor: "pointer",
            }}
          />
        </div>

        <img
          src={line1}
          alt="line1"
          style={{ position: "absolute", left: 0, top: 218.5 }}
        />
        <div
          style={{
            width: 305,
            textAlign: "left",
            position: "absolute",
            top: 236,
            left: 38,
          }}
        >
          <CategoryListLeft
            categories={["  "]}
            selectedCategory={selectedCategory}
            onCategoryClick={handleCategoryClick}
          />
        </div>
        <img
          src={line1}
          alt="line1"
          style={{ position: "absolute", left: 0, top: 309 }}
        />
        <br />
        <div
          style={{
            position: "absolute",
            left: 38,
            top: 324,
            display: "block",
            width: 305,
            textAlign: "left",
          }}
        >
          <CategoryListLeft
            categories={category_title}
            style={{ fontFamily: "category_other" }}
            selectedCategory={selectedCategory}
            onCategoryClick={handleCategoryClick}
          />
        </div>
        <br />
        <img
          src={line1}
          alt="line1"
          style={{ position: "absolute", left: 0, top: 772 }}
        />

      </div>
    </div>
  );
}

function CategoryListLeft({ categories, selectedCategory, onCategoryClick }) {
  return (
    <div>
      {categories.map((category) => (
        <CategoryTitle
          key={category}
          title={category}
          // selected prop 
          selected={selectedCategory === category}
          others={selectedCategory !== category}
          onClick={() => onCategoryClick(category)}
        />
      ))}
    </div>
  );
}

function CategoryTitle({ title, onClick, selected, others }) {
  return (
    <Title others={others} selected={selected} href="#" onClick={onClick}>
      {title}
    </Title>
  );
}

export default LeftCategories;
