import React, { useState } from 'react';
import axios from 'axios';  // axios   import.
import { useNavigate } from 'react-router-dom';

import LeftCategories from '../leftCategories';
import signupText from "../../image/signupText.png";
import signUpBtn from "../../image/signupBtn.png";
import signup_component from "../../image/signup_component.png";
import signUpGroup from "../../image/signUpGroup.png";
import passwordIsNotSame from "../../image/passwordIsNotSame.png";
import './SignUp.css';

function SignUpPage({ isLoggedIn, setLoginStatus, setClickedSignUp, setIsLoginClicked }) {
    const [nickname, setNickname] = useState('');
    const [username, setUsername] = useState('');
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [showPasswordError, setShowPasswordError] = useState(false);  //       
    const [selectedCategory, setSelectedCategory] = useState(null);
    const [selectedBookId, setSelectedBookId] = useState(null);

    const navigate = useNavigate();

    const signUpURL = '/user/register/';

    const handleSubmit = async (event) => {
        event.preventDefault();  // Form   

        if (password !== confirmPassword) {
            console.log(typeof (password));
            setShowPasswordError(true);  //    
            return;
        } else {
            setShowPasswordError(false);  //     
        }

        try {
            await axios.post(signUpURL, {
                'nickname': nickname,
                'username': username,
                'email': email,
                'password1': password,
                'password2': confirmPassword
            }, {
                headers: {
                    'Content-Type': 'application/json'
                }
            }
            );

            alert(" .");
            navigate('/');  //  
            setIsLoginClicked(true);

        } catch (error) {
            if (error.response && error.response.status === 400) {
                console.error(" ", error);
                alert(" .");
            } else {
                console.error(" ", error);
            }
        }
    }

    const handleCategoryChange = (category) => {
        setSelectedCategory(null);
    }


    const resetToInitialState = () => {
    };

    return (
        <div>
            <LeftCategories onCategoryChange={handleCategoryChange} onLogoClick={resetToInitialState} /> {/* onLogoClick prop  */}

            <div style={{
                position: "absolute",
                left: 377,
                top: 76,
            }}>


                <img
                    src={signupText}
                    alt="signupText"
                    style={{ position: "absolute", left: 72.5, top: 126 }}
                />

                <img
                    src={signUpGroup}
                    alt="signUpGroup"
                    style={{ position: "absolute", left: 750 - 304.5, top: 243 }}
                />


                {/* <img
                    src={successedSignUp}
                    alt="successedSignUp"
                    style={{ position: "absolute", left: 895 - 304.5, top: 854 }}
                /> */}



                <form onSubmit={handleSubmit}>
                    <input
                        type="text"
                        placeholder=" "
                        className="input-placeholder-color"
                        value={username}
                        onChange={e => setUsername(e.target.value)}
                        style={{
                            position: "absolute",
                            left: 975 - 304.5,
                            top: 261,
                            border: 'none', //  
                            outline: 'none', //     (    )
                            fontSize: '22px', // placeholder  
                            width: '350px', //  


                        }}
                    />
                    <input
                        type="text"
                        placeholder=" "
                        className="input-placeholder-color"
                        value={nickname}
                        onChange={e => setNickname(e.target.value)}
                        style={{
                            position: "absolute",
                            left: 975 - 304.5,
                            top: 371,
                            border: 'none', //  
                            outline: 'none', //     (    )
                            fontSize: '22px', // placeholder  
                            width: '350px', //  


                        }}
                    />
                    <input
                        type="text"
                        placeholder=" "
                        className="input-placeholder-color"
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                        style={{
                            position: "absolute",
                            left: 975 - 304.5,
                            top: 480,
                            border: 'none', //  
                            outline: 'none', //     (    )
                            fontSize: '22px', // placeholder  
                            width: '350px', //  


                        }}
                    />
                    <input
                        type="password"
                        placeholder=" "
                        className="input-placeholder-color"
                        value={password}
                        onChange={e => setPassword(e.target.value)}
                        style={{
                            position: "absolute",
                            left: 975 - 304.5,
                            top: 590,
                            border: 'none', //  
                            outline: 'none', //     (    )
                            fontSize: '22px', // placeholder  
                            width: '350px', //  


                        }}
                    />
                    <input
                        type="password"
                        placeholder="    "
                        className="input-placeholder-color"
                        value={confirmPassword}

                        onChange={e => setConfirmPassword(e.target.value)}
                        style={{
                            position: "absolute",
                            left: 975 - 304.5,
                            top: 701,
                            border: 'none', //  
                            outline: 'none', //     (    )
                            fontSize: '22px', // placeholder  
                            width: '350px', //  


                        }}
                    />

                    <button type="submit" style={{ display: 'none' }}></button>
                </form>

                {showPasswordError && (
                    <img
                        src={passwordIsNotSame}
                        alt="Password is not same"
                        style={{ position: "absolute", left: 967 - 304.5, top: 706 }}

                    />
                )}

                <img
                    src={signUpBtn}
                    alt="signUpBtn"
                    onClick={() => document.querySelector("button[type='submit']").click()}  //    
                    style={{ position: "absolute", left: 1016 - 304.5, top: 770, cursor: "pointer" }}
                />
            </div>

        </div>
    );
}

export default SignUpPage;