import React, { useState, useEffect } from 'react';
import axios from "axios";

import nickname from "../../image/nickname_change.png";
import nicknamechange from "../../image/nicknameText.png";
import check_nickname from "../../image/check_nickname.png";
import RectangleNick from "../../image/RectangleNick.png";
import inputing from "../../image/inputing.png";
import Line7 from "../../image/Line 7.png";
import hello from "../../image/helloText.png";
import nimText from "../../image/nimText.png";


function NickNameChange(userInfo) {
    const [usernameData, setUsernameData] = useState(''); //    state
    const [newNickname, setNewNickname] = useState(''); //    state


    const UpdateURL = '/user/mypage/update/';

    const userToken = localStorage.getItem('userToken'); // localStorage  



    useEffect(() => {
        setUsernameData(userInfo.userInfo.nickname);
    }, []);

    const handleSubmit = async () => {
        const data = {
            username: userInfo.userInfo.username,
            email: userInfo.userInfo.email,
            nickname: newNickname
        };

        //   
        const headers = {
            'Authorization': `Bearer ${userToken}` //  userToken userInfo  .
        };

        try {
            const response = await axios.put(UpdateURL, data, { headers: headers });
            if (response.status === 200) {
                alert(" .");
                setUsernameData(newNickname);  //   
                setNewNickname('');  // Input  

            }
        } catch (error) {
            alert("  .");
        }
    };

    //       
    const calculateNimTextPosition = (text) => {
        let byteLength = 0;

        for (let i = 0; i < text.length; i++) {
            if (escape(text.charAt(i)).length > 4) {
                byteLength += 2; //  2 
            } else {
                byteLength += 1; //  1 
            }
        }

        return 1100 + (30 * byteLength); //       
    };


    return (
        <div>
            <img
                src={hello}
                alt="hello"
                style={{ position: "absolute", left: 943, top: 326 }}
            />

            <span
                style={{
                    position: "absolute",
                    left: 1120,  // hello   5px .
                    top: 305,
                    color: "#3477CF",
                    fontSize: "45px",
                    fontWeight: 700,
                }}>
                {usernameData}
            </span>
            <img
                src={nimText}
                alt="nimText"
                style={{ position: "relative", left: calculateNimTextPosition(usernameData), top: 326 }}
            // usernameData  30px    hello   .
            />


            <img
                src={nickname}
                alt="nickname"
                style={{ position: "absolute", left: 377, top: 70 }}
            />
            <img
                src={nicknamechange}
                alt="nicknamechange"
                style={{ position: "absolute", left: 729, top: 490 }}
            />

            <img
                src={RectangleNick}
                alt="RectangleNick"
                style={{ position: "absolute", left: 906, top: 476 }}
            />
            <img
                src={RectangleNick}
                alt="RectangleNick"
                style={{ position: "absolute", left: 906, top: 601 }}
            />
            <img
                src={inputing}
                alt="inputing"
                style={{ position: "absolute", left: 929, top: 618 }}
            />
            <img
                src={Line7}
                alt="Line7"
                style={{ position: "absolute", left: 1087, top: 618 }}
            />



            <input
                type="text"
                placeholder="  ."
                value={newNickname}  // value  
                onChange={(e) => setNewNickname(e.target.value)}  //      
                style={{
                    position: "absolute",
                    left: 929,
                    top: 493,
                    border: 'none',
                    outline: 'none',
                    fontSize: '22px',
                    width: '300px',
                }}
            />
            <img
                src={check_nickname}
                onClick={handleSubmit}
                alt="check_nickname"
                style={{ position: "absolute", left: 1381, top: 481, cursor: 'pointer' }}
            />
        </div>
    );

}

export default NickNameChange;