import React, { useState } from 'react'; // useState 
import axios from 'axios';
import { useNavigate } from 'react-router-dom';

//  JSON !

import loginText from "../../image/login.png";
import loginBtn from "../../image/loginBtn.png";
import emailText from "../../image/emailText.png";
import passwordText from "../../image/passwordText.png";
import RectanglEmail from "../../image/RectangleNick.png";
import Rectanglpassword from "../../image/RectangleNick.png";
import ErrorLogin from "../../image/errorOptionLogin.png";
import LeftCategories from '../leftCategories.js';


function LoginPage() {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [loginError, setLoginError] = useState(false); //   
    const [selectedCategory, setSelectedCategory] = useState(null);
    const [selectedBookId, setSelectedBookId] = useState(null);
    const [isClickedSignUp, setClickedSignUp] = useState(false);
    const navigate = useNavigate();

    // const LoginURL = '/user/login/'; //     URL    .
    const LoginURL = '/user/login/';
    const MainPageURL = '/';


    const handleSubmit = async (event) => {
        event.preventDefault();  // Form   

        //      
        if (!email.trim() || !password.trim()) {
            alert("   .");
            return;
        }

        try {
            const response = await axios.post(LoginURL, {
                'email': email,
                'password': password,
            }, {
                headers: {
                    'Content-Type': 'application/json'
                }
            });

            const accessToken = response.data.token.access; //   access 
            localStorage.setItem('userToken', accessToken);
            navigate('/');

        } catch (error) {
            if (error.response) {
                switch (error.response.status) {
                    case 400:
                        alert("   ."); //     
                        setLoginError(true);
                        break;
                    case 401:
                        alert("   ");
                        break;
                    default:
                        alert("   .  .");
                }
            } else {
                console.error(" ", error);
            }
        }
    }

    const handleCategoryChange = (category) => {
        setSelectedCategory(category);
        setSelectedBookId(null);  // Reset selected book when changing category
    }


    const resetToInitialState = () => {
        setSelectedBookId(null);
        setSelectedCategory("  ");
        setClickedSignUp(false);
    };
    return (
        <div>
            <LeftCategories onCategoryChange={handleCategoryChange} onLogoClick={resetToInitialState} /> {/* onLogoClick prop  */}

            <div style={{
                position: "absolute",
                left: 377,
                top: 76,
            }}>
                <img
                    src={loginText}
                    alt="loginText"
                    style={{ position: "absolute", left: 72.5, top: 126 }}
                />
                <img
                    src={emailText}
                    alt="emailText"
                    style={{ position: "absolute", left: 531.5, top: 303 }}
                />

                <img
                    src={passwordText}
                    alt="passwordText"
                    style={{ position: "absolute", left: 505.5, top: 434 }}
                />

                <img
                    src={RectanglEmail}
                    alt="RectanglEmail"
                    style={{ position: "absolute", left: 647.5, top: 289 }}
                />
                <img
                    src={Rectanglpassword}
                    alt="Rectanglpassword"
                    style={{ position: "absolute", left: 647.5, top: 420 }}
                />


                <form onSubmit={handleSubmit}>
                    <input
                        type="text"
                        placeholder=" ."
                        value={email}
                        onChange={e => setEmail(e.target.value)}
                        style={{
                            position: "absolute",
                            left: 670.5,
                            top: 306,
                            border: 'none', //  
                            outline: 'none', //     (    )
                            fontSize: '22px', // placeholder  
                            width: '350px', //  
                        }}
                    />

                    <input
                        type="password"
                        placeholder=" ,"
                        value={password}
                        onChange={e => setPassword(e.target.value)}
                        style={{
                            position: "absolute",
                            left: 670.5,
                            top: 437,
                            border: 'none', //  
                            outline: 'none', //     (    )
                            fontSize: '22px', // placeholder  
                            width: '350px', //  
                        }}
                    />
                    <button type="submit" style={{ display: 'none' }}></button>

                </form>

                {loginError &&
                    (<img
                        src={ErrorLogin}
                        alt="Error during login"
                        style={{ position: "absolute", left: 906 - 304.5, top: 553 }} />)}


                <img
                    src={loginBtn}
                    alt="loginBtn"
                    onClick={() => document.querySelector("button[type='submit']").click()}  //    
                    style={{ position: "absolute", left: 723.5, top: 610, cursor: "pointer" }}
                />

            </div>
        </div>
    );

}

export default LoginPage;